import React, { Component, Fragment } from "react"

import Navbar from "../Navbar"
import Menubar from "../Menubar"
import { Breadcrumb } from "react-bootstrap"
import { Link } from "@reach/router"
import { BreadCrumbContainer } from "./style"
import { IBreadCrumbOption } from "../../../interfaces"

interface IProps {
	breadCrumbList: IBreadCrumbOption[]
	translateSlug?: string
}

const Header = (props: IProps) => {
	const { breadCrumbList, translateSlug } = props

	const getBreadCrumbItem = (item: IBreadCrumbOption) => {
		return item.isActive ? (
			<Breadcrumb.Item key={item.name}>
				<Link to={item.url}>{item.name}</Link>
			</Breadcrumb.Item>
		) : (
			<Breadcrumb.Item active key={item.name}>
				{item.name}
			</Breadcrumb.Item>
		)
	}
	return (
		<React.Fragment>
			<Navbar translateSlug={translateSlug} />
			<Menubar />
			{breadCrumbList && breadCrumbList.length > 0 ? (
				<BreadCrumbContainer>
					<Breadcrumb>
						{breadCrumbList.map(item => getBreadCrumbItem(item))}
					</Breadcrumb>
				</BreadCrumbContainer>
			) : null}
		</React.Fragment>
	)
}

export default Header
