import styled from "styled-components"

export const SocialList = styled.ul`
	display: flex;
	flex-wrap: wrap;

	@media (max-width: 576px) {
		justify-content: center;
	}
`

export const SocialItem = styled.li`
	list-style-type: none;
	margin: 24px 8px 0 8px;

	a {
		color: #333;
	}

	span {
		margin-left: 5px;
		color: #fff;
		font-size: 20px;
		height: 25px;
	}
`

export const ContactList = styled.ul`
	width: 260px;
	margin: 8px 0 16px auto;
	padding-bottom: 32px;

	@media (max-width: 576px) {
		width: 100%;
		text-align: center;
	}
`

export const ContactItem = styled.li`
	list-style-type: none;
	margin-right: 8px;
	margin-left: 8px;

	a {
		color: white;
		text-decoration: none;
	}

	span {
		margin-left: 8px;
		font-family: "Montserrat", sans-serif;
		font-size: 14px;
	}
`

export const Button = styled.div`
	width: 165px;
	text-align: center;

	@media (max-width: 576px) {
		margin: 0 auto;
	}
`

export const FooterContainer = styled.div`
	background: #13414f;
	color: #fff;
	position: relative;
	overflow: auto;
	margin: 0;
	padding-top: 40px;
	bottom: 0;
	min-width: 100%;
	min-height: 3vh;
	background-image: url("https://www.kivalitaconsulting.com/wp-content/uploads/2019/06/leao-sobre-21-2-min.png");
	background-repeat: no-repeat;
	background-position-x: 50%;
	background-position-y: 50%;
	background-size: contain;

	@media (max-width: 576px) {
		background-position-y: -12%;

		.wrapper-logo {
			text-align: center;
			margin-bottom: 32px;
		}
	}
`

export const FooterRights = styled.div`
	background: rgb(17, 89, 100);
	color: #fff;
	display: flex;
	flex-direction: row;
	justify-content: center;
	justify-items: center;
	padding-top: 5px;
	font-size: 13px;
`
