import React from "react"

import { useTranslation } from "react-i18next"

import { FaWhatsapp, FaPhone, FaEnvelope } from "react-icons/fa"

import {
	Container,
	ContactList,
	ContactItem,
	FlagsItem,
	FlagsList,
} from "./styles"

import UsFlag from "../../../assets/us.png"
import BrFlag from "../../../assets/br.png"

interface IProps {
	translateSlug?: string
}

const Navbar = ({ translateSlug }: IProps) => {
	const { t, i18n } = useTranslation()

	const changeLanguage = (lng: string) => {
		const url = window.location.href

		if (lng == i18n.language) return

		if (url.includes("legislation", 0)) {
			window.location.replace(
				translateSlug
					? `${location.origin}/legislation-articles/${translateSlug}`
					: `${location.origin}/legislation-articles`
			)
		} else if (url.includes("article", 0)) {
			window.location.replace(
				translateSlug
					? `${location.origin}/articles/${translateSlug}`
					: `${location.origin}/articles`
			)
		} else if (url.includes("news", 0)) {
			window.location.replace(
				translateSlug
					? `${location.origin}/news/${translateSlug}`
					: `${location.origin}/news`
			)
		} else if (url.includes("qualificacao-de-fornecedores", 0)) {
			window.location.reload()
		}

		i18n.changeLanguage(lng)
	}

	return (
		<div>
			<Container>
				<div className="container">
					<div className="row align-items-center">
						<ContactList>
							<ContactItem>
								<a href="https://api.whatsapp.com/send?phone=5511943068327">
									<span>
										<FaWhatsapp />
									</span>
									<span>+55 (11) 9 4306-8327</span>
								</a>
							</ContactItem>
							<ContactItem>
								<a href="tel:551137210708">
									<span>
										<FaPhone />
									</span>
									<span>+55 (11) 3721-0708</span>
								</a>
							</ContactItem>
							<ContactItem>
								<a href="tel:551137210604">
									<span>
										<FaPhone />
									</span>
									<span>+55 (11) 3721-0604</span>
								</a>
							</ContactItem>
							<ContactItem>
								<a href="mailto:contato@kivalita.com.br">
									<span>
										<FaEnvelope />
									</span>
									<span>contato@kivalita.com.br</span>
								</a>
							</ContactItem>
						</ContactList>
						{/* Flags */}
						<FlagsList>
							<FlagsItem>
								<span>
									<img
										src={BrFlag}
										alt="pt"
										width="48px"
										height="48"
										onClick={() => changeLanguage("pt")}
									/>
								</span>
							</FlagsItem>
							<FlagsItem>
								<span>
									<img
										src={UsFlag}
										alt="en"
										width="48px"
										height="48"
										onClick={() => changeLanguage("en")}
									/>
								</span>
							</FlagsItem>
						</FlagsList>
					</div>
				</div>
			</Container>
		</div>
	)
}

export default Navbar
