import React from "react"

import Header from "./Header"
import Footer from "./Footer"

import "../../styles/global.css"

import "../utils/i18n"
import "./layout.css"
import { IBreadCrumbOption } from "../../interfaces"
import CookieConsent from "react-cookie-consent"
import { translate } from "../../components/utils/translate"
import styled from "styled-components"
import {
	FaLinkedinIn,
	FaTwitter,
	FaFacebookSquare,
	FaInstagram,
	FaRegNewspaper,
} from "react-icons/fa"
import SocialLinks from "./Menubar/components/SocialLinks"

interface IProps {
	breadCrumbList: IBreadCrumbOption[]
	translateSlug?: string
	children: any
}

const Layout = (props: IProps) => {
	const { breadCrumbList, translateSlug, children } = props

	return (
		<div>
			<div id="header">
				<Header breadCrumbList={breadCrumbList} translateSlug={translateSlug} />
			</div>
			<div className="content">
				<CookieConsent
					location="top"
					buttonText={translate("cookie.accept")}
					declineButtonText={translate("cookie.decline")}
					enableDeclineButton
					onDecline={() => {}}
					cookieName="gatsby-gdpr-google-analytics"
					style={{ background: "#f39438" }}
					buttonStyle={{
						background: "#13414f",
						color: "white",
						fontSize: "13px",
						fontWeight: "bold",
					}}
					declineButtonStyle={{
						background: "white",
						color: "#4c4a4a",
						fontSize: "13px",
						fontWeight: "bold",
					}}
				>
					{translate("cookie.message")}
				</CookieConsent>
				<SocialLinks />
				{children}
				<Footer />
			</div>
		</div>
	)
}

export default Layout
