import React from "react"
import { Modal } from "react-responsive-modal"
import useScript from "../../../../hooks/external-scripts"

interface IProps {
	showModal: boolean
	onCloseModal(): void
}

const FormNewsletter: React.FC<IProps> = ({ onCloseModal, showModal }) => {
	const modalUrl =
		"new RDStationForms('newsletter-botao-266ac4faa2b0a3b042d7', 'UA-156947544-1').createForm()"

	useScript(
		"https://d335luupugsy2.cloudfront.net/js/rdstation-forms/stable/rdstation-forms.min.js",
		true
	)
	useScript(modalUrl, false)

	return (
		<Modal open={showModal} onClose={onCloseModal} center>
			<div role="main" id="newsletter-botao-266ac4faa2b0a3b042d7"></div>
		</Modal>
	)
}

export default FormNewsletter
