import React, { useState } from "react"
import styled from "styled-components"
import {
	FaLinkedinIn,
	FaTwitter,
	FaFacebookSquare,
	FaInstagram,
	FaRegNewspaper,
	FaYoutube,
} from "react-icons/fa"
import FormNewsletter from "../../../utils/Form/FormNewsletter"

const SocialList = styled.div`
	@media (max-width: 768px) {
		display: none;
	}

	position: fixed;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
	z-index: 9999;
	padding: 10px;
	border-top-left-radius: 15px;
	border-bottom-left-radius: 15px;
	right: -5px;
	background-color: #ffffffc0;
`

const SocialItem = styled.div`
	a {
		display: block;
		text-align: center;
		padding: 4px 10px 10px 10px;
		transition: all 0.3s ease;
		color: white;
		font-size: 20px;
		margin-bottom: 10px;
		border-radius: 5px;
	}

	a:hover {
		background: #f4bf8c;
	}

	.facebook {
		background: #3b5998;
		color: white;
	}

	.twitter {
		background: #55acee;
		color: white;
	}

	.google {
		background: #dd4b39;
		color: white;
	}

	.linkedin {
		background: #007bb5;
		color: white;
	}

	.instagram {
		background: #f09433;
		background: -moz-linear-gradient(
			45deg,
			#f09433 0%,
			#e6683c 25%,
			#dc2743 50%,
			#cc2366 75%,
			#bc1888 100%
		);
		background: -webkit-linear-gradient(
			45deg,
			#f09433 0%,
			#e6683c 25%,
			#dc2743 50%,
			#cc2366 75%,
			#bc1888 100%
		);
		background: linear-gradient(
			45deg,
			#f09433 0%,
			#e6683c 25%,
			#dc2743 50%,
			#cc2366 75%,
			#bc1888 100%
		);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f09433', endColorstr='#bc1888',GradientType=1 );
		color: white;
	}

	.youtube {
		background: red;
		color: white;
	}
`

const SocialLinks: React.FC = () => {
	const [showModal, setShowModal] = useState(false)

	const openModal = () => setShowModal(true)
	const closeModal = () => setShowModal(false)

	return (
		<>
			{showModal && (
				<FormNewsletter showModal={showModal} onCloseModal={closeModal} />
			)}
			<SocialList>
				<SocialItem>
					<a
						href="https://www.linkedin.com/company/kivalita-consulting/"
						target="_blank"
						className="linkedin"
					>
						<span>
							<FaLinkedinIn />
						</span>
					</a>
				</SocialItem>
				<SocialItem>
					<a
						className="twitter"
						href="https://twitter.com/Kivalita"
						target="_blank"
					>
						<span>
							<FaTwitter />
						</span>
					</a>
				</SocialItem>
				<SocialItem>
					<a
						className="facebook"
						href="https://www.facebook.com/kivalitaconsulting"
						target="_blank"
					>
						<span>
							<FaFacebookSquare />
						</span>
					</a>
				</SocialItem>
				<SocialItem>
					<a
						className="instagram"
						href="https://www.instagram.com/kivalitaconsulting/"
						target="_blank"
					>
						<span>
							<FaInstagram />
						</span>
					</a>
				</SocialItem>
				<SocialItem>
					<a
						className="youtube"
						href="https://www.youtube.com/channel/UCLKWJojHTneN8yA1_wMjG6A/featured"
						target="_blank"
					>
						<span>
							<FaYoutube />
						</span>
					</a>
				</SocialItem>
			</SocialList>
		</>
	)
}

export default SocialLinks
