import { useEffect } from "react"

const useScript = (url: string, isUrl: boolean) => {
	useEffect(() => {
		const script = document.createElement("script")

		script.async = true

		if (isUrl) {
			script.src = url
		} else {
			setTimeout(() => {
				script.append(url)
			}, 3000)
		}
		document.body.appendChild(script)

		return () => {
			document.body.removeChild(script)
		}
	}, [url])
}

export default useScript
