import React from "react"
import { Link } from "gatsby"

import LogoMenu from "../../../assets/logo-hori.svg"

import { ContainerLogo, MenuItem, Container } from "./styles"

import { IMenubar } from "../../../interfaces/index"

import MENU_DATA from "../../../data/menu.json"
import { translate } from "../../utils/translate"
import { Nav, Navbar, NavDropdown } from "react-bootstrap"

const Menubar = () => {
	return (
		<Container>
			<Navbar collapseOnSelect expand="lg" bg="light">
				<ContainerLogo>
					<Link to="/">
						<img src={LogoMenu} alt="Logotipo Kivalita" />
					</Link>
				</ContainerLogo>
				<Navbar.Toggle aria-controls="responsive-navbar-nav" color="black" />
				<Navbar.Collapse id="responsive-navbar-nav">
					<Nav className="mr-auto">
						{MENU_DATA.map((data: IMenubar) => {
							if (data.subMenu && data.urlDirection === "solutions") {
								return (
									<NavDropdown
										title={translate(`menu.${data.title}`)}
										id="collasible-nav-dropdown"
									>
										<NavDropdown.Item key={data.id}>
											<MenuItem>
												<Link to={`/${data.urlDirection}`}>
													{translate(`menu.${data.title}`)}
												</Link>
											</MenuItem>
										</NavDropdown.Item>
										{data.subMenu?.map(item => {
											if (item.externalLink) {
												return (
													<Nav.Link
														href={item.urlDirection}
														style={{ marginLeft: 15 }}
														target="blank"
													>
														<MenuItem key={item.id}>
															<Link> {translate(`menu.${item.title}`)}</Link>
														</MenuItem>
													</Nav.Link>
												)
											} else {
												return (
													<NavDropdown.Item key={item.id}>
														<MenuItem>
															<Link to={`/${item.urlDirection}`}>
																{translate(`menu.${item.title}`)}
															</Link>
														</MenuItem>
													</NavDropdown.Item>
												)
											}
										})}
									</NavDropdown>
								)
							} else if (data.subMenu && data.urlDirection !== "solutions") {
								console.log(data)
								return (
									<NavDropdown
										title={translate(`menu.${data.title}`)}
										id="collasible-nav-dropdown"
									>
										{data.subMenu?.map(item => {
											if (item.externalLink) {
												return (
													<Nav.Link
														href={item.urlDirection}
														style={{ marginLeft: 15 }}
														target="blank"
													>
														<MenuItem key={item.id}>
															<Link> {translate(`menu.${item.title}`)}</Link>
														</MenuItem>
													</Nav.Link>
												)
											} else {
												return (
													<NavDropdown.Item key={item.id}>
														<MenuItem>
															<Link to={`/${item.urlDirection}`}>
																{translate(`menu.${item.title}`)}
															</Link>
														</MenuItem>
													</NavDropdown.Item>
												)
											}
										})}
									</NavDropdown>
								)
							} else {
								if (data.externalLink) {
									return (
										<Nav.Link key={data.id} href={data.urlDirection}>
											<MenuItem key={data.id}>
												<Link>{translate(`menu.${data.title}`)}</Link>
											</MenuItem>
										</Nav.Link>
									)
								} else {
									return (
										<Nav.Link key={data.id}>
											<MenuItem key={data.id}>
												<Link to={`/${data.urlDirection}`}>
													{translate(`menu.${data.title}`)}
												</Link>
											</MenuItem>
										</Nav.Link>
									)
								}
							}
						})}
					</Nav>
				</Navbar.Collapse>
			</Navbar>
		</Container>
	)
}

export default Menubar
