import React from "react"

import {
	FaLinkedinIn,
	FaTwitter,
	FaFacebookSquare,
	FaInstagram,
	FaWhatsapp,
	FaPhone,
	FaEnvelope,
	FaMapMarkerAlt,
} from "react-icons/fa"

import {
	SocialList,
	SocialItem,
	ContactList,
	ContactItem,
	Button,
	FooterContainer,
	FooterRights,
} from "./styles"

import Logo from "../../../assets/logo-vertical-branca-2.svg"
import { translate } from "../../utils/translate"

const scrollToTop = () => {
	document.body.scrollTop = 0
	document.documentElement.scrollTop = 0
}

export default function Footer() {
	return (
		<div>
			<FooterContainer>
				<div className="container">
					<div className="row">
						<div className="col-sm-12 col-md-6 col-lg-6 wrapper-logo">
							<img src={Logo} width={200} alt="Logotipo Kivalita" />
							<SocialList>
								<SocialItem>
									<a href="https://www.linkedin.com/company/kivalita-consulting/">
										<span>
											<FaLinkedinIn />
										</span>
									</a>
								</SocialItem>
								<SocialItem>
									<a href="https://twitter.com/Kivalita">
										<span>
											<FaTwitter />
										</span>
									</a>
								</SocialItem>
								<SocialItem>
									<a href="https://www.facebook.com/kivalitaconsulting">
										<span>
											<FaFacebookSquare />
										</span>
									</a>
								</SocialItem>
								<SocialItem>
									<a href="https://www.instagram.com/kivalitaconsulting/">
										<span>
											<FaInstagram />
										</span>
									</a>
								</SocialItem>
							</SocialList>
							<Button>
								<button
									style={{ backgroundColor: "#F4BF8C", color: "#FFF" }}
									className="btn"
									onClick={() => scrollToTop()}
								>
									{translate("footer.button")}
								</button>
							</Button>
						</div>

						<div className="col-sm-12 col-md-6 col-lg-6">
							<ContactList>
								<h6>{translate("footer.contact")}</h6>
								<ContactItem>
									<a href="https://api.whatsapp.com/send?phone=5511943068327">
										<FaWhatsapp /> <span>+55 (11) 9 4306-8327</span>
									</a>
								</ContactItem>
								<ContactItem>
									<a href="tel:551137210708">
										<FaPhone /> <span>+55 (11) 3721-0708</span>
									</a>
								</ContactItem>
								<ContactItem>
									<a href="tel:551137210604">
										<FaPhone /> <span>+55 (11) 3721-0604</span>
									</a>
								</ContactItem>
								<ContactItem>
									<a href="mailto:contato@kivalita.com.br">
										<FaEnvelope /> <span>contato@kivalita.com.br</span>
									</a>
								</ContactItem>
							</ContactList>

							<ContactList>
								<h6>{translate("footer.address")}</h6>
								<ContactItem>
									<FaMapMarkerAlt />
									<span>
										Rua dos Três Irmãos, 310 - Sala 302 - Vila Progredior
										<br />
										São Paulo - SP
									</span>
								</ContactItem>
							</ContactList>
						</div>
					</div>
				</div>
			</FooterContainer>
			<FooterRights>
				© 2020 Kivalita Consulting | Todos os direitos reservados
			</FooterRights>
		</div>
	)
}
